import React from "react";
import PropTypes from "prop-types";
import { AppBar, Drawer, Fab, makeStyles, Toolbar } from "@material-ui/core";
import { ExpandLess, ExpandMore } from "@material-ui/icons";
import Summary from "./Summary";

const useStyles = makeStyles({
	appBar: {
		top: "auto",
		bottom: 0
	},
	appBarOpened: {
		top: "auto"
	},
	drawerContent: {
		paddingTop: "65px"
	},
	drawerPaper: {
		maxHeight: "80%"
	},
	drawerPaperOverflow: {
		maxHeight: "80%",
		overflowY: "visible"
	},
	fabButton: {
		position: "absolute",
		zIndex: 1,
		top: -30,
		left: 0,
		right: 0,
		margin: "0 auto"
	}
});

const BottomBar = (props) => {
	const classes = useStyles();

	const [open, setOpen] = React.useState(false);
	const [hideOpened, setHideOpened] = React.useState(true);
	const [hideClosed, setHideClosed] = React.useState(false);
	const [showOverflow, setShowOverflow] = React.useState(false);

	React.useEffect(() => {
		if (showOverflow) {
			setTimeout(() => {
				setShowOverflow(false);
			}, 450);
		}
	}, [showOverflow]);

	const toggleDrawer = (state) => {
		setShowOverflow(!showOverflow);
		if (state) {
			setHideClosed(true);
			setHideOpened(false);
		} else {
			setTimeout(() => {
				setHideOpened(true);
				setHideClosed(false);
			}, 300);
		}
		setOpen(state);
	};
	return (
		<div>
			<AppBar position="fixed" color="primary" className={classes.appBar} style={{ display: hideClosed ? "none" : "flex" }}>
				<Toolbar>
					<Fab color="secondary" aria-label="open drawer" onClick={() => toggleDrawer(true)} className={classes.fabButton}>
						<ExpandLess />
					</Fab>
				</Toolbar>
			</AppBar>
			<React.Fragment key="bottom">
				<Drawer
					id="drawer"
					anchor="bottom"
					classes={showOverflow ? { paper: classes.drawerPaperOverflow } : { paper: classes.drawerPaper }}
					open={open}
					onClose={() => toggleDrawer(false)}
					transitionDuration={400}
				>
					<div
						role="presenation"
					>
						<AppBar position="fixed" color="primary" className={classes.appBarOpened} style={{ display: hideOpened ? "none" : "flex" }}>
							<Toolbar>
								<Fab color="secondary" aria-label="close drawer" onClick={() => toggleDrawer(false)} className={classes.fabButton}>
									<ExpandMore />
								</Fab>
							</Toolbar>
						</AppBar>
						<div className={classes.drawerContent}>
							<Summary
								handlePackageClick={props.handlePackageClick}
								packages={props.packages}
							/>
						</div>
					</div>
				</Drawer>
			</React.Fragment>
		</div>
	);
};

BottomBar.propTypes = {
	handlePackageClick: PropTypes.func,
	packages: PropTypes.array
};

export default BottomBar;